
import React from 'react';
import useBEM from '../../utils/hooks/useBEM';

interface Props {
    type?: 'primary' | 'secondary',
    className?: string,
    disabled?: boolean,

    click?: () => void
}

const Button : React.FC<Props>  = ({
    children,
    type = 'primary',
    className,
    disabled,

    click
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('button');

    // HOOKS

    // METHODS
    const classModifier = () => {
        const modifiers = [type as string];
        if(disabled)modifiers.push('disabled');
        return modifiers;
    }

    const onClickHandler = () => {
        if(click)click();
    }

    return (
        <button 
            onClick={onClickHandler}
            disabled={disabled}
            className={B(classModifier()) +' '+ (className || '')}>
            {children}
        </button>
    )
}

export default Button;