import React, { useState } from 'react';
import { useHistory } from 'react-router';
import Button from '../../components/ui/Button';
import GeneralQuestions from '../../constants/questions.constant';
import useBEM from '../../utils/hooks/useBEM';
import QuestionsNav from './components/QuestionsNav';
import QuestionsPanel from './components/QuestionsPanel';

interface Props {

}

const QuestionnairesPage : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('questionnaires-page');
    const general = GeneralQuestions;
    const history = useHistory();

    // TEMPORARY
    const [activeQuestionIndex, setActiveQuestionIndex] = useState(0);

    // HOOKS

    // METHODS
    const previousQuestion = () => {
        if(activeQuestionIndex === 0){
            history.push('/');
            return;
        }
        setActiveQuestionIndex(prev => prev - 1)
    }

    const nextQuestion = () => {
        if(general.questions.length === (activeQuestionIndex+1)){
            history.push('/closing');
            return;
        }
        setActiveQuestionIndex(prev => prev + 1)
    }

    return (
        <div className={B()}>
            <QuestionsNav numberOfPage={general.questions.length} activePage={activeQuestionIndex}/>

            {/* TEMPORARY activeQuestionIndex */}
            <QuestionsPanel 
                activeQuestionIndex={activeQuestionIndex}
                questions={general.questions}/>

            <div className={E('buttons')}>
                <Button click={previousQuestion} type="secondary">BACK</Button>
                <Button click={nextQuestion}>NEXT</Button>
            </div>
        </div>
    )
}

export default QuestionnairesPage;