import React, { useState } from 'react';
import Button from '../../components/ui/Button';
import Logo from '../../components/ui/Logo';
import useBEM from '../../utils/hooks/useBEM';
import Disclaimer from './components/disclaimer';

interface Props {

}

const HomePage : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('home-page');
    const [accepted, setAccepted] = useState(false);
    const [showDisclaimer, setShowDisclaimer] = useState(false);

    // HOOKS

    // METHODS
    const start = () => {
        setShowDisclaimer(true);
    }

    const onAcceptDisclaimer = () => {
        setAccepted(!accepted)
    }

    return (
        <div className={B()}>    
            
            <Logo/>

            <div className={E('popup', showDisclaimer ? 'hide' : '')}>                
                <h1 className={E('popup-title')}>Waiting for you turn?</h1>
                <p className={E('popup-text')}>
                    Thank you for scanning this QR Code. You are about to begin your AVA™ precision experience.
                </p>
                <p className={E('popup-text')}>
                    To help us provide you with an optional and personalized experience, we invite you to answer a few questions.
                </p>
                <Button type="primary" click={start}>Start your questionnaire</Button>
            </div>
            
            <Disclaimer show={showDisclaimer} accepted={accepted} onAccept={onAcceptDisclaimer}/>

        </div>
    )
}

export default HomePage;