import React from 'react';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {
    activePage: number,
    numberOfPage: number
}

const QuestionsNav : React.FC<Props>  = ({
    activePage,
    numberOfPage
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('questions-nav');

    // HOOKS

    // METHODS

    return (
        <div className={B()}>
            {
                new Array(numberOfPage).fill('').map((o,index: number) => 
                
                    <div className={E('dot', index <= activePage ? 'done' : '')}/>

                )
            }
        </div>
    )
}

export default QuestionsNav;