import React from 'react';
import { useHistory } from 'react-router';
import Button from '../../../components/ui/Button';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {
    accepted: boolean,
    onAccept: () => void,
    show: boolean
}

const Disclaimer : React.FC<Props>  = ({
    accepted,
    onAccept,
    show
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('disclaimer');
    const history = useHistory();

    // HOOKS

    // METHODS
    const gotoToQuestionnaires = () => {
        history.push('/questions');
    }

    return (
        <div className={B( show ? 'show' : '' )}>
            <h2 className={E('title')}>Legal notices</h2>

            <p className={E('text')}>
                The purpose of the questionaire is to provide you with recommendation based on personal data, vision challenges and hobbies, These data are not stored. They are used to give a personalised recommendation.
            </p>

            <p className={E('text', ['accept', accepted ? 'checked' : ''])} onClick={onAccept}>
                I understand and accept the <strong>disclaimer</strong>.
            </p>

            <Button disabled={!accepted} type="primary" click={gotoToQuestionnaires}>CONTINUE</Button>
        </div>
    )
}

export default Disclaimer;